.privacy {
  width: 1300px;
  margin: 0 auto;

  @media (max-width: @desktop-width) {
    width: 1060px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-title {
  margin: 150px 0 50px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 68px;

  @media (max-width: @desktop-width) {
    margin: 100px 0 50px;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
    margin: 70px 0 50px;
    font-size: 36px;
    line-height: 49px;
  }

  @media (max-width: @mobile-width) {
    margin: 50px 0;
  }
}
.privacy-text {
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }
}
.privacy-subtitle {
  margin: 50px 0 30px;
  font-family: "Satoshi-Bold", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;

  @media (max-width: @desktop-width) {
    
  }

  @media (max-width: @tablet-width) {
    font-size: 24px;
    line-height: 32px;
  }
}
.privacy-lists {
  display: block;
}
.privacy-items {
  margin-left: 30px;
  width: 1300px;
  list-style: disc;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    width: 1060px;
    font-size: 16px;
    line-height: 140%;
  }

  @media (max-width: @tablet-width) {
    width: 700px;
  }

  @media (max-width: @mobile-width) {
    width: 328px;
  }
}
.privacy-link {
  display: block;
  width: 100px;
  height: 20px;
  margin: 86px 0;
  text-align: right;
  font-family: "Satoshi-Regular", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;

  @media (max-width: @desktop-width) {
    font-size: 16px;
    line-height: 140%;
  }
}